/**
 * HTML 문자열에서 첫 번째 <img> 태그의 src 값을 반환합니다.
 * 이미지 태그가 없거나 src가 비어있으면 빈 문자열을 반환합니다.
 * 브라우저 환경에서는 DOMParser를 우선 사용하고, 없는 경우 정규식을 사용합니다.
 */
export default function getImgFromHtml(html: string | undefined | null): string {
  if (!html) return ''

  // 브라우저 환경에서 DOM 파서를 사용 (jsdom이 있는 테스트 환경에서도 동작)
  try {
    if (typeof DOMParser !== 'undefined') {
      const parser = new DOMParser()
      const doc = parser.parseFromString(html, 'text/html')
      const img = doc.querySelector('img')
      return img?.getAttribute('src') ?? ''
    }
  } catch (err) {
    // DOMParser 사용 실패시 아래 정규식 방식으로 시도
  }

  // 정규식 방식: 첫 번째 src 속성 값 캡쳐
  const regex = /<img[^>]*\ssrc=["']?([^"'\s>]+)["']?[^>]*>/i
  const match = html.match(regex)
  if (match && match[1]) return match[1]

  return ''
}
