/**
 * HTML 문자열을 plain text로 변환하는 함수
 * @param html - 변환할 HTML 문자열
 * @returns 변환된 plain text 문자열
 */
const htmlToPlainText = (html: string): string => {
  if (!html) return ''

  // 1. HTML 엔티티를 먼저 디코딩
  const entityMap: { [key: string]: string } = {
    '&nbsp;': ' ',
    '&amp;': '&',
    '&lt;': '<',
    '&gt;': '>',
    '&quot;': '"',
    '&#39;': "'",
    '&apos;': "'",
    '&cent;': '¢',
    '&pound;': '£',
    '&yen;': '¥',
    '&euro;': '€',
    '&copy;': '©',
    '&reg;': '®'
  }

  let text = html

  // HTML 엔티티 변환
  Object.keys(entityMap).forEach(entity => {
    const regex = new RegExp(entity, 'g')
    text = text.replace(regex, entityMap[entity])
  })

  // 2. 특정 태그들을 공백으로 변환
  text = text
    .replace(/<br\s*\/?>/gi, ' ') // <br> 태그를 공백으로
    .replace(/<\/p>/gi, ' ') // </p> 태그를 공백으로
    .replace(/<\/div>/gi, ' ') // </div> 태그를 공백으로
    .replace(/<\/h[1-6]>/gi, ' ') // 제목 태그들을 공백으로
    .replace(/<\/li>/gi, ' ') // </li> 태그를 공백으로
    .replace(/<hr\s*\/?>/gi, ' ') // <hr> 태그를 공백으로
    .replace(/<\/blockquote>/gi, ' ') // </blockquote> 태그를 공백으로

  // 3. 모든 HTML 태그 제거
  text = text.replace(/<[^>]*>/g, '')

  // 4. 모든 줄바꿈을 공백으로 변환하고 연속된 공백 정리
  text = text
    .replace(/\n/g, ' ') // 모든 줄바꿈을 공백으로
    .replace(/[ \t]+/g, ' ') // 연속된 공백을 하나로
    .trim() // 앞뒤 공백 제거

  return text
}

/**
 * DOM을 사용한 HTML to plain text 변환 (브라우저 환경에서만 사용 가능)
 * @param html - 변환할 HTML 문자열
 * @returns 변환된 plain text 문자열
 */
export const htmlToPlainTextWithDOM = (html: string): string => {
  if (!html) return ''

  try {
    // 1. HTML 태그들 사이에 공백 추가 (DOM 파싱 전 전처리)
    const preprocessedHtml = html
      .replace(/<\/h[1-6]>/gi, '</h> ') // 제목 태그 끝에 공백
      .replace(/<\/p>/gi, '</p> ') // 문단 태그 끝에 공백
      .replace(/<\/div>/gi, '</div> ') // div 태그 끝에 공백
      .replace(/<\/li>/gi, '</li> ') // 리스트 아이템 끝에 공백
      .replace(/<\/blockquote>/gi, '</blockquote> ') // 인용구 끝에 공백
      .replace(/<br\s*\/?>/gi, '<br> ') // br 태그에 공백
      .replace(/<hr\s*\/?>/gi, '<hr> ') // hr 태그에 공백

    const tempDiv = document.createElement('div')
    tempDiv.innerHTML = preprocessedHtml
    const plainText = tempDiv.textContent || tempDiv.innerText || ''

    // 2. 모든 줄바꿈을 공백으로 변환하고 연속된 공백 정리
    return plainText
      .replace(/\n/g, ' ') // 모든 줄바꿈을 공백으로
      .replace(/[ \t]+/g, ' ') // 연속된 공백을 하나로
      .trim() // 앞뒤 공백 제거
  } catch (error) {
    // DOM이 사용불가능한 환경에서는 정규식 방법 사용
    return htmlToPlainText(html)
  }
}
