interface DeleteConferenceRoundsProps {
  ids: number[]
}

const deleteConferenceRoundsAPICall = async (
  token: string,
  clubId: number,
  conferenceId: number,
  { ids }: DeleteConferenceRoundsProps
) => {
  await axios.delete<AxiosResponse<null>>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/conference/${conferenceId}/round`,
    {
      headers: { Authorization: token },
      params: { ids }
    }
  )
}

export const useDeleteConferenceRounds = (
  clubId: number,
  conferenceId: number
): UseMutationResult<void, unknown, DeleteConferenceRoundsProps, void> => {
  const { enqueueSnackbar } = useSnackbar()

  const token = useAuthHeader()()
  const queryClient = useQueryClient()
  const mutationFn = (values: DeleteConferenceRoundsProps) =>
    deleteConferenceRoundsAPICall(token, clubId, conferenceId, values)
  return useMutation(mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(CONFERENCE_MESSAGE.DELETE_SUCCESS, { variant: 'success' })
    },
    onError: err => {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    },
    onSettled: () => {
      queryClient.invalidateQueries('conferenceRounds')
    }
  })
}

