import { Box, Card, Stack, Typography } from '@mui/material'

import { type ClubReview } from 'src/responses/review'

import ReviewKeyword from '../ReviewKeyword'

const ReviewCard = (review: ClubReview) => {
  const { name, description, year, semester, content, keywords, recommendedPerson, userNickname } = review

  const semesterLabel = `${year.toString().slice(2)}-${semester === 'SPRING' ? '1' : '2'}`

  return (
    <Card
      sx={{
        p: '34px 36px 13px',
        borderRadius: '10px',
        boxShadow: '0px 2px 2px 0px rgba(159, 162, 191, 0.25), 0px 9px 16px 0px rgba(159, 162, 191, 0.18)',
        display: 'flex',
        flexDirection: 'column',
        gap: '14px'
      }}
    >
      <Stack direction="row" justifyContent="space-between" alignItems="flex-start">
        <Stack spacing="3px">
          {name && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '13px',
                fontWeight: 400,
                color: '#000000',
                lineHeight: 1.2
              }}
            >
              {name}
            </Typography>
          )}
          {description && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '14px',
                fontWeight: 400,
                color: 'rgba(34, 51, 84, 0.7)',
                lineHeight: 1.2
              }}
            >
              {description}
            </Typography>
          )}
        </Stack>
        <Box
          sx={{
            backgroundColor: 'rgba(34, 51, 84, 0.1)',
            borderRadius: '16px',
            padding: '3px 7px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          <Typography
            sx={{
              fontFamily: 'Pretendard',
              fontSize: '13px',
              fontWeight: 400,
              color: '#000000'
            }}
          >
            {semesterLabel}
          </Typography>
        </Box>
      </Stack>

      <Typography
        sx={{
          fontFamily: 'Pretendard',
          fontSize: '13px',
          lineHeight: '1.5',
          color: '#000000'
        }}
      >
        {content}
      </Typography>

      {keywords && keywords.length > 0 && (
        <Stack spacing="3px">
          {keywords.map(keyword => (
            <ReviewKeyword key={keyword.id} keyword={keyword} />
          ))}
        </Stack>
      )}

      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ pt: 0.5 }}>
        <Typography
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '13px',
            color: '#FF9393'
          }}
        >
          {recommendedPerson ? `# ${recommendedPerson}에게 추천!` : ''}
        </Typography>
        <Typography
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '14px',
            color: '#B1B1B1'
          }}
        >
          from. {userNickname}
        </Typography>
      </Stack>
    </Card>
  )
}

export default ReviewCard
