import { Link } from 'react-router-dom'

import { Box, Card, CardActionArea, Chip, Stack, Typography } from '@mui/material'

import getImgFromHtml from 'src/utils/getImgFromHtml'
import { htmlToPlainTextWithDOM } from 'src/utils/htmlToPlainText'

import type { PublicConferenceRound } from 'src/models/conference'

interface ConferencePreviewCardProps {
  club: { id: number; name?: string; description?: string }
  conferenceRound: PublicConferenceRound
}

const ConferencePreviewCard = ({ club, conferenceRound }: ConferencePreviewCardProps) => {
  const plainText = htmlToPlainTextWithDOM(conferenceRound.note)
  const imageUrl = getImgFromHtml(conferenceRound.note) || ''
  const date = new Date(conferenceRound.startAt).toLocaleDateString()

  return (
    <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <CardActionArea
        component={Link}
        to={`/club/${club.id}/conference/round/${conferenceRound.id}`}
        sx={{ height: '100%' }}
      >
        <Stack spacing={1.2} sx={{ padding: 2, flex: 1, display: 'flex', flexDirection: 'column' }}>
          {/* 동아리 정보 */}
          {club.name && (
            <Stack marginBottom={0.4}>
              <Typography
                variant="body1"
                sx={{
                  color: 'text.primary',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }}
              >
                {club.name}
              </Typography>
              {club.description && (
                <Typography
                  variant="body2"
                  color="text.secondary"
                  sx={{
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap'
                  }}
                >
                  {club.description}
                </Typography>
              )}
            </Stack>
          )}

          {/* 컨퍼런스 타이틀 */}
          <Typography
            variant="h4"
            sx={{
              color: 'text.primary',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap'
            }}
          >
            {conferenceRound.conferenceTitle}
          </Typography>

          {/* 카테고리 칩들 */}
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
            {conferenceRound.categories.map((category, index) => (
              <Chip
                key={index}
                label={category}
                size="small"
                variant="filled"
                sx={{
                  height: '24px',
                  fontSize: '0.75rem',
                  '& .MuiChip-label': {
                    padding: '0 8px'
                  }
                }}
              />
            ))}
          </Box>

          {/* Note 텍스트 (최대 2줄) */}
          <Typography
            variant="body1"
            sx={{
              minHeight: 'calc(1.5em * 2)', // 1줄은 1.5em이므로 2줄 높이
              display: '-webkit-box',
              WebkitLineClamp: 2,
              WebkitBoxOrient: 'vertical',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              color: 'text.primary'
            }}
          >
            {plainText}
          </Typography>

          {/* 이미지 영역 */}
          {imageUrl ? (
            <Box
              component="img"
              src={imageUrl}
              alt="conference round content"
              sx={{
                width: '100%',
                height: '200px',
                objectFit: 'cover',
                borderRadius: 1
              }}
            />
          ) : (
            <Box
              sx={{
                width: '100%',
                height: '200px'
              }}
            />
          )}

          {/* 날짜 */}
          <Typography variant="caption" sx={{ color: 'text.secondary', marginTop: 'auto' }}>
            {date}
          </Typography>
        </Stack>
      </CardActionArea>
    </Card>
  )
}

export default ConferencePreviewCard
